import { Component } from 'react'
import { Link } from 'react-router-dom'
import './header.scss'

class Header extends Component {
    render() {
        return (
            <div className="header">
                <div className="container">
                    <Link to="/" className="header_link">Home</Link>
                    <Link to="/Movies" className="header_link">Movies</Link>
                    <Link to="/Popular" className="header_link">Popular</Link>
                    <Link to="/Tvshows" className="header_link">Tvshows</Link>
                    <Link to="/Upcoming" className="header_link">Upcoming</Link>
                </div>
            </div>
        );
    }
}

export default Header