import { Component } from 'react'
import { Link } from 'react-router-dom'
import './TvShowCard.scss'

class TvShowCard extends Component {
    render() {
        return (
            <Link to={`/tvshows/${this.props.id}`} className="movie-card">
                <div className="img-holder">
                    <img src={`https://image.tmdb.org/t/p/w500/${this.props.imgLink}`} />
                </div>
                <p className="rating" >{this.props.movieRating}</p>
                <div className="title-holder">
                    <h2 className="title">{this.props.movieTitle}</h2>
                    <span className="release-date">{this.props.releaseDate}</span>
                </div>
            </Link>
        )
    }
}

export default TvShowCard