import { Component } from 'react';
import './SingleTvShow.scss'

class SingleTvShowPage extends Component{
    render () {
        return (
            <div className="wrapper">
                <div className="left">
                    <img src={`https://image.tmdb.org/t/p/w500/${this.props.imgLink}`} alt=""/>
                </div>
                <div className="right">
                    <h1>{this.props.title}</h1>
                    <span>
                        {this.props.releaseDate}
                    </span>
                    <p>
                        {this.props.overview}
                    </p>
                </div>
            </div>
        )
    }
}

export default SingleTvShowPage