import { Component } from 'react'
import Header from '../container/header'
import axios from 'axios'
import TvshowCard from '../components/TvShowCard'

class TvShows extends Component {

    constructor() {
        super();
        this.state = {
            MovieInfo: {
                isFetched: false,
                data: [],
                error: null
            }
        }
    }

    componentDidMount() {
        let self = this;
        axios.get(`https://api.themoviedb.org/3/tv/popular`, {
            params: {
                api_key: '017ad4bace8b4554e9bc42c73c4a392b'
            }
        })
            .then(function (response) {
                self.setState({
                    MovieInfo: {
                        isFetched: true,
                        data: response.data.results,
                        error: false
                    }
                })
            })
            .catch(function (error) {
                self.setState({
                    MovieInfo: {
                        isFetched: true,
                        data: [],
                        error: error
                    }
                })
            })
            .then(function () {

            });
    }


    render() {
        return (
            <>
                <Header />
                <div className="container">
                    {
                        this.state.MovieInfo.isFetched ? (
                            <div className="movies-wrapper">
                                {
                                    this.state.MovieInfo.data.map((tvShow, index) => (
                                        <TvshowCard
                                            id={tvShow.id}
                                            imgLink={tvShow.backdrop_path}
                                            movieTitle={tvShow.original_name}
                                            releaseDate={tvShow.first_air_date}
                                            movieRating={tvShow.vote_average}
                                            key={index}
                                        />
                                    ))
                                }
                            </div>
                        ) : (
                            <h1>Loading..</h1>
                        )
                    }
                </div>
            </>
        );
    }
}

export default TvShows