import { Component } from "react";
import './SingleMoviePage.scss'

class SingleMoviePage extends Component {
    render() {
        return (
            <div className="info-movie">
                <div className="left">
                    <img src={`https://image.tmdb.org/t/p/w500/${this.props.imgLink}`} />
                </div>
                <div className="right">
                    <h1>{this.props.MovieTitle}</h1>
                    <span>{this.props.releaseDate}</span>
                    <p>{this.props.overview}</p>
                </div>
            </div>
        )
    }
}

export default SingleMoviePage