import { Component } from 'react'
import Header from '../container/header'
import axios from 'axios'
import SingleMoviePage from '../container/SingleMoviePage'
import MovieCard from '../components/MovieCard'
import ActorCard from '../components/ActorsCard'


class SingleMovies extends Component {
    constructor() {
        super();
        this.state = {
            MovieInfo: {
                isFetched: false,
                data: [],
                error: null
            },
            reccomendList: {
                isFetched: false,
                data: [],
                error: null
            },
            actorList: {
                isFetched: false,
                data: [],
                error: null,
            }
        }
    }

    componentDidMount() {
        let self = this;
        const { match } = this.props

        axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}`, {
            params: {
                api_key: '017ad4bace8b4554e9bc42c73c4a392b'
            }
        })
            .then(function (response) {
                self.setState({
                    MovieInfo: {
                        isFetched: true,
                        data: response.data,
                        error: false
                    }
                })
            })
            .catch(function (error) {
                self.setState({
                    MovieInfo: {
                        isFetched: true,
                        data: [],
                        error: error
                    }
                })
            })
            .then(function () {

            });

        axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/recommendations`, {
            params: {
                api_key: '017ad4bace8b4554e9bc42c73c4a392b'
            }
        })
            .then(function (response) {
                self.setState({
                    reccomendList: {
                        isFetched: true,
                        data: response.data.results,
                        error: false
                    }
                })
            })
            .catch(function (error) {
                self.setState({
                    reccomendList: {
                        isFetched: true,
                        data: [],
                        error: error
                    }
                })
            })
            .then(function () {

            });
        axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/credits`, {
            params: {
                api_key: '017ad4bace8b4554e9bc42c73c4a392b'
            }
        })
            .then(function (response) {
                self.setState({
                    actorList: {
                        isFetched: true,
                        data: response.data.cast,
                        error: false
                    }
                })
            })
            .catch(function (error) {
                self.setState({
                    actorList: {
                        isFetched: true,
                        data: [],
                        error: error
                    }
                })
            })
            .then(function () {

            });
    }

    componentDidUpdate(prevProps) {
        const { match } = this.props
        let self = this

        if (match.params.id != prevProps.match.prevProps) {
            axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}`, {
                params: {
                    api_key: '017ad4bace8b4554e9bc42c73c4a392b'
                }
            })
                .then(function (response) {
                    self.setState({
                        MovieInfo: {
                            isFetched: true,
                            data: response.data,
                            error: false
                        }
                    })
                })
                .catch(function (error) {
                    self.setState({
                        MovieInfo: {
                            isFetched: true,
                            data: [],
                            error: error
                        }
                    })
                })
                .then(function () {

                });

            axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/recommendations`, {
                params: {
                    api_key: '017ad4bace8b4554e9bc42c73c4a392b'
                }
            })
                .then(function (response) {
                    self.setState({
                        reccomendList: {
                            isFetched: true,
                            data: response.data.results,
                            error: false
                        }
                    })
                })
                .catch(function (error) {
                    self.setState({
                        reccomendList: {
                            isFetched: true,
                            data: [],
                            error: error
                        }
                    })
                })
                .then(function () {

                });
            axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/credits`, {
                params: {
                    api_key: '017ad4bace8b4554e9bc42c73c4a392b'
                }
            })
                .then(function (response) {
                    self.setState({
                        actorList: {
                            isFetched: true,
                            data: response.data.cast,
                            error: false
                        }
                    })
                })
                .catch(function (error) {
                    self.setState({
                        actorList: {
                            isFetched: true,
                            data: [],
                            error: error
                        }
                    })
                })
                .then(function () {

                });
        }
    }

    render() {
        return (
            <>
                <Header />
                <div className="container">
                    {
                        this.state.MovieInfo.isFetched ? (
                            <SingleMoviePage
                                imgLink={this.state.MovieInfo.data.backdrop_path}
                                MovieTitle={this.state.MovieInfo.data.original_title}
                                releaseDate={this.state.MovieInfo.data.release_date}
                                overview={this.state.MovieInfo.data.overview}
                            />
                        ) : (
                            <h1>Loading...</h1>
                        )
                    }
                    {
                        this.state.reccomendList.isFetched ? (
                            <>
                                <h2>Similar Movies</h2>
                                <div className="movies-wrapper">
                                    {
                                        this.state.reccomendList.data.map((movie, index) => (
                                            <MovieCard
                                                id={movie.id}
                                                imgLink={movie.backdrop_path}
                                                movieTitle={movie.original_title}
                                                releaseDate={movie.release_date}
                                                movieRating={movie.vote_average}
                                                key={index}
                                            />
                                        ))
                                    }
                                </div>
                            </>
                        ) : (
                            <h1>bye</h1>
                        )
                    }
                    {
                        this.state.actorList.isFetched ? (
                            <>
                                <h1>Actors</h1>
                                <div className="actor-wrapper">
                                    {
                                        this.state.actorList.data.map((item) => (
                                            <ActorCard
                                                name={item.name}
                                                imgLink={item.profile_path}
                                                id={item.id}
                                            />
                                        ))
                                    }
                                </div>
                            </>
                        ) : (
                            <h1>kemadi okam</h1>
                        )
                    }

                </div>
            </>
        );
    }
}

export default SingleMovies