import { Component } from 'react'
import Header from '../container/header'
import axios from 'axios'
import SingleTvShowPage from '../container/SingleTvShowPage';

class SingleTvShow extends Component {

    constructor() {
        super();
        this.state = {
            TvShowInfo: {
                isFetched: false,
                data: [],
                error: null
            }
        }
    }

    componentDidMount() {
        let self = this;

        const { match } = this.props

        axios.get(`https://api.themoviedb.org/3/tv/${match.params.id}`, {
            params: {
                api_key: '017ad4bace8b4554e9bc42c73c4a392b'
            }
        })
            .then(function (response) {
                self.setState({
                    TvShowInfo: {
                        isFetched: true,
                        data: response.data,
                        error: false
                    }
                })
            })
            .catch(function (error) {
                self.setState({
                    TvShowInfo: {
                        isFetched: true,
                        data: [],
                        error: error
                    }
                })
            })
            .then(function () {

            });
    }

    render() {
        return (
            <>
                <Header />
                <div className="container">
                    {
                        this.state.TvShowInfo.isFetched ? (
                            <>
                                <SingleTvShowPage
                                    imgLink={this.state.TvShowInfo.data.backdrop_path}
                                    title={this.state.TvShowInfo.data.original_name}
                                    overview={this.state.TvShowInfo.data.overview}
                                    releaseDate={this.state.TvShowInfo.data.last_air_date}
                                />
                            </>
                        ) : (
                            <h1>Malumot kemadi</h1>
                        )
                    }
                </div>
            </>
        );
    }
}

export default SingleTvShow